@font-face {
  src: url('fonts/InputSans-Regular-Edu.eot'); /* IE < 9 */
  src: url('fonts/InputSans-Regular-Edu.eot?#') format("embedded-opentype"), /* IE 9 */
       url('fonts/InputSans-Regular-Edu.woff2') format("woff2"),
       url('fonts/InputSans-Regular-Edu.woff') format("woff");
  font-family: 'InputSans';
  font-style: normal;
  font-weight: normal;
}

@font-face {
  src: url('fonts/InputSerif-Regular-Edu.eot'); /* IE < 9 */
  src: url('fonts/InputSerif-Regular-Edu.eot?#') format("embedded-opentype"), /* IE 9 */
       url('fonts/InputSerif-Regular-Edu.woff2') format("woff2"),
       url('fonts/InputSerif-Regular-Edu.woff') format("woff");
  font-family: 'InputSerif';
  font-style: normal;
  font-weight: normal;
}

a:hover {
  color: #fff;
  text-decoration: none;
}

.navbar {
  background: #1c2237;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
}

.container {
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  font-family: 'InputSans';
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: end;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
}

.nav-item:hover {
  border-bottom: 2px solid #f00946;
}

.nav-links {
  color: #fff !important;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  font-family: 'InputSans';
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
}

.logo {
  display: inline-block;
  background-size: 300px;
  margin-right: 5px;
  height: 42px;
  width: 84px;
}



@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #1c2237;
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #f00946;
    transform: scale(1.2);
    transition: all 0.3s ease;
    text-decoration: none;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-item {
    width: 100%;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 1.0rem;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
  }


}
